
.container {
  width: 100%;
  max-width: 600px;
  margin-top: 30px;
}
main {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  margin: 20px auto;
}
p.block {
  position: absolute;
  margin: 0;
  background-repeat: no-repeat;
  display: inline-block;
  transition: all .2s;
}
.controls {
  width: 100%;
}
.controls button {
  font-size: 24px;
  margin-left: 30px;
  margin-right: 30px;
}



